.change-box-bottom-btn{
    display: flex;
    gap: 10px;
    flex-grow: 1;
}
.change-box-bottom-btn > a{
    flex-grow: 1;
    text-align: center;
    justify-content: center;
}

.three-dif-btn a{
    font-weight: bold !important;
    font-size: 1.2rem;
}
.three-dif-btn{
    max-width: 80%;
}

.share_detail {
    display: flex;
    justify-content: space-between;
    margin-left: 5px;
}

.stauts-bullet li::marker{
    color: #FF01EB !important;
}
.stauts-bullet li{
    margin-bottom: 8px;
}
.stauts-bullet .share_detail > div span:first-child{
    margin-right: 8px;
}


.pr-2 {
    padding-right: 16px;
}

.first-btn {
    background: linear-gradient(90deg, #5BFE26 0%, #ADFD37 55.18%, #EFFC45 100%) !important;
    color: black !important;
}

.second-btn {
    background: linear-gradient(270deg, #F044E8 0%, #6A51C9 100%) !important;
}

.third-btn {
    background: linear-gradient(270deg, #FE8642 0%, #E7251F 100%) !important;
}
.stats-modal {
    .modal-dialog {
        max-width: 600px !important;
        div {
            font-family: Poppins-Light;
        }
        span, p {
            font-family: Poppins-Light;
        }
        .three-dif-btn {
            margin-bottom: 20px;
            .tip-btn {
                padding: 14px 30px;
                border: none;
                color: black;
                background: linear-gradient(90deg, #5BFE26 0%, #ADFD37 55.18%, #EFFC45 100%) !important;
                &:hover {
                    background: linear-gradient(90deg, #b8ffa1 0%, #9ae923 55.18%, #e9e630 100%) !important;
                }
            }
            .block-user-btn {
                padding: 14px 30px;
                border: none;
                color: white;
                background: linear-gradient(270deg, #FE8642 0%, #E7251F 100%) !important;
                &:hover {
                    background: linear-gradient(270deg, #ff782a 0%, #ff4741 100%) !important;
                }
            }
            .add-friend-btn {
                border: none;
                padding: 14px 30px !important;
            
            }
        }
        
    }
}